<script>
  let responsive = false;
  let toggleResponsive = () => {
    responsive = !responsive;
  };
</script>

<style>
  .topnav {
    overflow: hidden;
    background-color: rgba(19, 19, 20, 0);
    margin-bottom: 16px;
  }

  .topnav a#brand {
    font-size: 1.5em;
  }

  .topnav a {
    float: left;
    display: block;
    color: rgb(10, 10, 10);
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    font-weight: bold;
  }

  .topnav a:hover {
    background-color: #ddd;
    color: rgb(245, 146, 33);
  }

  .topnav a.active {
    color: black;
  }

  .topnav a:not(:first-child) {
    display: none;
  }

  .topnav a.icon {
    float: right;
    display: block;
  }

  .topnav.responsive {
    position: relative;
  }

  .topnav.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .topnav.responsive a {
