      display: none;
    }
  }
</style>

<div class="topnav" class:responsive id="myTopnav">
  <a id="brand" href="#home" class="active">Koffi</a>
  <a href="#news">About</a>
  <a href="#contact">Menu</a>
  <a href="#about">Services</a>
  <a href="#contact">Contact</a>
  <a href="javascript:void(0);" class="icon" on:click={toggleResponsive}>
    <i class="fa fa-bars" />
  </a>
</div>
